.contacts {
    background-color: white;
}

.contacts__container {
    display: flex;
    justify-content: space-between;

    h2 {
        margin-bottom: 48px;
    }

    aside {
        font-size: 24px;
        font-weight: 400;
        margin-bottom: 8px;
    }
}

.contacts__info {
    max-width: 420px;
}

.contacts__socials {
    display: flex;
    gap: 16px;
    align-items: center;
}

.contacts__phone {
    font-size: 32px;
    font-weight: 600;
}

.contacts__messengers {
    display: flex;
    gap: 8px;
}

.contacts__link {
    width: 48px;
    height: 48px;
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: var(--transition);

    &:first-child {
        background-color: #29A71A;
    }

    &:last-child {
        background-color: #039BE5;
    }
    
    svg {
        fill: white;
        transition: var(--transition);
    }

    &:hover {
        background-color: #E6720526;

        svg {
            fill: var(--orange);
        }
    }
}

.contacts__item {
    display: flex;
    flex-direction: column;
    gap: 4px;
    font-size: 24px;
    font-weight: 400;
    margin: 24px 0;

    p:first-child {
        opacity: 0.6;
    }
}

.contacts__map {
    max-width: 815px;
    aspect-ratio: 815/505;
    border-radius: 16px;
}